.switch-wrap {
    position: fixed;
    top: 92px;
    right: 13px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 28px;
  }
  
.switch input { 
    opacity: 0;
    width: 0;
    height: 0;
}
  
.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--light-color);
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 28px;
}
  
.slider:before {
    position: absolute;
    content: "";
    height: 21px;
    width: 21px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 50%;
}
  
input:checked + .slider {
    background-color: var(--dark-color);
}
  
input:focus + .slider {
    box-shadow: 0 0 1px var(--dark-color);
}
  
input:checked + .slider:before {
    -webkit-transform: translateX(21px);
    -ms-transform: translateX(21px);
    transform: translateX(21px);
}
  
